body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgb(112, 106, 106);
  height: 80px;
  padding: 10px;
  color: white;
  z-index: 800;
}

.App-title {
  font-size: 1em;
}

.App-help {
  display: flex;
  align-items: center;
  justify-content: center;
}

.App-help div {
  margin: 0;
  text-align: left;
}

.App-help div.App-help-click {
  margin-right: 14px;
  text-align: right;
}

.App-help-item {
  display: flex;
}

.App-help-click {
  width: 250px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.leaflet-top, .leaflet-bottom {
  position: fixed !important;
}

.leaflet-top {
  top: 100px !important;
}

.weather-Popup .leaflet-popup-content-wrapper {
  min-width: 360px;
}

.weather-Root .leaflet-container {
  height: 800px;
}

.leaflet-popup-content {
  width: inherit !important;
  margin: 2px 22px 20px 22px !important;
}

.leaflet-search-icon {
  cursor: pointer;
}

.leaflet-search-icon:hover {
  background-color: #e2e2e2;
}

.jsonTable {
  border: 2px solid #aaa;
  padding: 2px;
  margin-top: 20px;
}

.jsonOdd {
  background: #eee;
}

.jsonCell {
  font-family: Courier New, Courier, monospace;
}

.jsonCell_value {
  min-width: 180px;
}

.jsonCell_name {
  font-weight: 600;
  min-width: 120px;
}
